import React, { memo } from "react";
import PropTypes from "prop-types";
import { Redirect, Switch } from "react-router-dom";
import PropsRoute from "../../shared/components/PropsRoute";
import Home from "./home/Home";
import SellPropertyPage from "./home/SellPropertyPage";
import BuyPropertyPage from "./home/BuyPropertyPage";
import NriServices from "./home/NriServices";

function Routing(props) {
  const { selectHome } = props;
  return (
    <Switch>
      <PropsRoute path="/home/sell-property" component={SellPropertyPage} />
      <PropsRoute path="/home/buy-property" component={BuyPropertyPage} />
      <PropsRoute path="/home/nri-services" component={NriServices} />
      <PropsRoute path="/" component={Home} selectHome={selectHome} />
      <Redirect to={"/"} />
    </Switch>
  );
}

Routing.propTypes = {
  blogposts: PropTypes.arrayOf(PropTypes.object),
  selectHome: PropTypes.func.isRequired,
  selectBlog: PropTypes.func.isRequired,
};

export default memo(Routing);
