import React, {Component} from "react";
import {toAbsoluteUrl} from "../../../app/utils/UtilMethods";
import {Button, Card, CardContent, Typography, withStyles,} from "@material-ui/core";

import Flippy, {BackSide, FrontSide} from "react-flippy";

const styles = (theme) => ({
  cardWrapper: {
    maxWidth: "750px",
  },
  image: {
    maxWidth: "100%",
    maxHeight: "50%",
    verticalAlign: "middle",
    // borderRadius: "20px",
    // boxShadow: theme.shadows[4],
  },
});

class BuyPropertyCards extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isFlipped: false,
    };
  }

  toggleFlip = () =>
    this.setState((prevState) => {
      return { isFlipped: !prevState.isFlipped };
    });

  render() {
    const { cardData = {}, classes = {} } = this.props;
    const { isFlipped } = this.state;
    const {
      image = "",
      subTitle = "",
      title = "",
      description = "",
      imageStyle = {},
    } = cardData;
    return (
      <>
        <Card
          className={classes.cardWrapper}
          data-aos="zoom-in-up"
          data-aos-delay={"300"}
        >
          <Flippy
            flipOnHover={false} // default false
            flipOnClick={true} // default false
            flipDirection="horizontal" // horizontal or vertical
            style={{ height: "350px" }}
            isFlipped={isFlipped}
          >
            <FrontSide>
              {image && (
                <div className={"text-center"}>
                  <img
                    src={toAbsoluteUrl(image)}
                    alt={"Image Alt"}
                    className={classes.image}
                    style={imageStyle}
                  />
                  {title && (
                    <Typography
                      variant="h5"
                      className={
                        "text-center align-items-center justify-content-center text-danger mt-1"
                      }
                    >
                      <strong>{title}</strong>
                    </Typography>
                  )}
                  <Button
                    onClick={() => this.toggleFlip()}
                    variant="outlined"
                    color="secondary"
                    className={"mt-2"}
                  >
                    More
                  </Button>
                </div>
              )}
            </FrontSide>
            <BackSide>
              <CardContent
                className={
                  "sm-p-top text-center align-items-center justify-content-center"
                }
              >
                <Typography
                  gutterBottom
                  variant="h6"
                  className={
                    "text-align-center align-items-center justify-content-center"
                  }
                >
                  {subTitle}
                </Typography>
                {title && (
                  <Typography
                    variant="h5"
                    className={
                      "text-center align-items-center justify-content-center text-danger mt-1"
                    }
                  >
                    <strong>{title}</strong>
                  </Typography>
                )}
                {description && (
                  <Typography
                    variant="body1"
                    className={
                      "text-center align-items-center justify-content-center"
                    }
                  >
                    {description}
                  </Typography>
                )}
                <Button
                  onClick={() => this.toggleFlip()}
                  variant="outlined"
                  color="secondary"
                  className={"mt-4"}
                >
                  Close
                </Button>
              </CardContent>
            </BackSide>
          </Flippy>
          {/*{image && (*/}
          {/*  <ZoomImage*/}
          {/*    src={toAbsoluteUrl(image)}*/}
          {/*    alt={"Image Alt"}*/}
          {/*    className={classes.image}*/}
          {/*    style={imageStyle}*/}
          {/*  />*/}
          {/*)}*/}
          {/*<CardContent*/}
          {/*  className={*/}
          {/*    "sm-p-top text-center align-items-center justify-content-center"*/}
          {/*  }*/}
          {/*>*/}
          {/*  <Typography*/}
          {/*    gutterBottom*/}
          {/*    variant="h6"*/}
          {/*    className={*/}
          {/*      "text-align-center align-items-center justify-content-center"*/}
          {/*    }*/}
          {/*  >*/}
          {/*    {subTitle}*/}
          {/*  </Typography>*/}
          {/*  {title && (*/}
          {/*    <Typography*/}
          {/*      variant="h5"*/}
          {/*      className={*/}
          {/*        "text-center align-items-center justify-content-center text-danger"*/}
          {/*      }*/}
          {/*    >*/}
          {/*      <strong>{title}</strong>*/}
          {/*    </Typography>*/}
          {/*  )}*/}
          {/*  {description && (*/}
          {/*    <Typography*/}
          {/*      variant="body1"*/}
          {/*      className={*/}
          {/*        "text-center align-items-center justify-content-center"*/}
          {/*      }*/}
          {/*    >*/}
          {/*      {description}*/}
          {/*    </Typography>*/}
          {/*  )}*/}
          {/*</CardContent>*/}
        </Card>
      </>
    );
  }
}

export default withStyles(styles, { withTheme: true })(BuyPropertyCards);
